import React from "react";

const TableInput = ({ params, onChange }) => (
  <table className="lab__table">
    <thead>
    <tr>
      {Object.keys(params).map((param, index) => (
        <th key={index} className={"lab__table-th lab__table-th_width_" + Object.keys(params).length}>{param}</th>
      ))}
    </tr>
    </thead>
    <tbody>
    <tr>
      {Object.keys(params).map(param => (
        <td key={param} className={"lab__table-td lab__table-td_width_" + Object.keys(params).length}>
          <input
            type="text"
            className="lab__table-input"
            value={params[param]}
            onChange={e => onChange(param, e.target.value)}
          />
        </td>
      ))}
    </tr>
    </tbody>
  </table>
);

export default TableInput;
