import { parse } from "mathjs";

export default function createIntegral(formula, formulaParam, definite, lowerBound, upperBound) {
  return (
    "\\int"
    + (definite
        ? (
          ("_{" + lowerBound + "}")
          + ("^{" + upperBound + "}")
        )
        : ""
    )
    + (" " + parse(formula).toTex())
    + (" d" + formulaParam)
  );
}
