import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";

import Home from "../modules/home";
import Lab1 from "../modules/lab1";
import Lab2 from "../modules/lab2";
import Lab3 from "../modules/lab3";
import Lab4 from "../modules/lab4";
import Lab5 from "../modules/lab5";
import Lab6 from "../modules/lab6";

const routes = [
  { name: "Главная", path: "", exact: true, component: Home, navVisible: false },
  { name: "1. Численное дифференцирование", path: "lab1", component: Lab1 },
  { name: "2. Численное интегрирование", path: "lab2", component: Lab2 },
  { name: "3. Метод Эйлера", path: "lab3", component: Lab3 },
  { name: "4. Метод Рунге-Кутта", path: "lab4", component: Lab4 },
  { name: "5. Метод Адамса", path: "lab5", component: Lab5 },
  { name: "6. Метод Милна", path: "lab6", component: Lab6 },
];

const Router = () => (
  <Switch>
    {routes.map(route => (
      <Route key={route.path} path={"/" + route.path} exact={route.exact} component={route.component}/>
    ))}

    <Redirect to={"/" + routes[0].path}/>
  </Switch>
);

export { routes, Router };
