import { parse } from "mathjs";
import React from "react";
import { InlineMath } from "react-katex";

const TableResult = ({ columns, rows, x, accuracy }) => (
  <table className="lab__table">
    <thead>
    <tr>
      {columns.map((column, index) => (
        <th
          key={index}
          className={"lab__table-th lab__table-th_width_" + columns.length + (column.type ? " lab__table-th_type_" + column.type : "")}
        >
          {column.type === "formula"
            ? (
              <InlineMath>
                {parse(column.value).toTex().replace(column.replaceValue, column.replaceFunction)}
              </InlineMath>
            )
            : column.value}
        </th>
      ))}
    </tr>
    </thead>
    <tbody>
    {rows.map((value, rowIndex) => (
      <tr key={"row-" + rowIndex}>
        {columns.map((column, columnIndex) => (
          <td key={"col-" + columnIndex} className={"lab__table-td lab__table-td_width_" + columns.length}>
            {column.func(columnIndex ? x : value, value).toFixed(accuracy)}
          </td>
        ))}
      </tr>
    ))}
    </tbody>
  </table>
);

export default TableResult;
