import React from "react";
import { Link } from "react-router-dom";

import { routes } from "../../router/router";

import "./nav.scss";

const Nav = () => (
  <nav className="nav">
    <ul className="nav__list">
      {routes.filter(route => typeof route.navVisible === "boolean" ? route.navVisible : true).map(route => (
        <li key={route.path} className="nav__item">
          <Link to={"/" + route.path} className="nav__item-link">{route.name}</Link>
        </li>
      ))}
    </ul>
  </nav>
);

export default Nav;
