import React, { useState } from "react";

import solveMiln from "../../../helpers/solveMiln";

import Lab from "../../components/lab";
import { InlineMath } from "react-katex";

export default function Lab6() {
  const [result] = useState(solveMiln(0, 1, .1, (x, y) => 2 * x ** 2 + 2 * y, 0, 1));

  const [resultX] = useState(result[0]);
  const [resultXmaxIndex] = useState(resultX.length - 1);
  const [resultY] = useState(result[1]);
  const [resultF] = useState(result[2]);

  const [columns] = useState([
    { value: "k" },
    { value: "x_k" },
    { value: "y_k" },
    { value: "f_k" }
  ]);
  const [rounding] = useState(6);

  return (
    <Lab
      title="Лабораторная работа 6. Метод Милна"
      goal="изучить метод Милна для решения дифференциальных уравнений"
      tasks={[
        {
          condition: "Решить задачу Коши:",
          formula: "\\frac{dy}{dx}=f(x,y)=2x^2 + 2y",
          params: [
            {
              name: "y(0)",
              value: 1
            },
            {
              name: "h",
              value: .1
            },
            {
              name: "a",
              value: 0
            },
            {
              name: "b",
              value: 1
            }
          ]
        }
      ]}
      conclusion={
        "В ходе работы был изучен метод Милна для решения дифференциальных уравнений."
        + " На основе полученных теоретических знаний были получены практические навыки благодаря применению метода для решения диффенциальных уравнений."
      }
    >
      <div className="lab__result">
        <table className="lab__table lab-6__table">
          <thead>
          <tr>
            {columns.map((column, columnIndex) => (
              <th
                key={columnIndex}
                className={"lab__table-th lab-6__table-th lab__table-th_width_" + columns.length + (column.type ? " lab__table_th_type_" + column.type : "")}
              >
                <InlineMath>{column.value}</InlineMath>
              </th>
            ))}
          </tr>
          </thead>
          <tbody>
          {
            resultX.map((x, index) => (
              <tr key={index}>
                <td className="lab__table-td">{index}</td>
                <td className="lab__table-td">{x && x.toFixed(rounding)}</td>
                <td className={"lab__table-td" + (index === resultXmaxIndex ? " lab__table-td_type_result" : "")}>{resultY[index] && resultY[index].toFixed(rounding)}</td>
                <td className="lab__table-td">{resultF[index] && resultF[index].toFixed(rounding)}</td>
              </tr>
            ))
          }
          </tbody>
        </table>
      </div>
    </Lab>
  )
};
