import React, { useState } from "react";
import { InlineMath } from "react-katex";

import solveAdams from "../../../helpers/solveAdams";

import Lab from "../../components/lab";

export default function Lab5() {
  const [result] = useState(solveAdams(0, 1, .1, (x, y) => 2 * x ** 2 + 2 * y, 0, 1));
  const [resultX] = useState(result[0]);
  const [resultXmaxIndex] = useState(resultX.length - 1);
  const [resultY] = useState(result[1]);
  const [resultDeltaY] = useState(result[2]);
  const [resultF] = useState(result[3]);
  const [resultQ] = useState(result[4]);
  const [resultDeltaQ] = useState(result[5]);
  const [resultDelta2Q] = useState(result[6]);
  const [resultDelta3Q] = useState(result[7]);
  const [columns] = useState([
    { value: "k" },
    { value: "x_k" },
    { value: "y_k" },
    { value: "Δy_k" },
    { value: "f_k" },
    { value: "q_k" },
    { value: "Δq_k" },
    { value: "Δ^2q_k" },
    { value: "Δ^3q_k" }
  ]);
  const [rounding] = useState(6);

  return (
    <Lab
      title="Лабораторная работа 5. Метод Адамса"
      goal="изучить метод Адамса для решения дифференциальных уравнений"
      tasks={[
        {
          condition: "Решить задачу Коши:",
          formula: "\\frac{dy}{dx}=f(x,y)=2x^2 + 2y",
          params: [
            {
              name: "y(0)",
              value: 1
            },
            {
              name: "h",
              value: .1
            },
            {
              name: "a",
              value: 0
            },
            {
              name: "b",
              value: 1
            }
          ]
        }
      ]}
      conclusion={
        "В ходе работы был изучен метод Адамса для решения дифференциальных уравнений."
        + " На основе полученных теоретических знаний были получены практические навыки благодаря применению метода для решения диффенциальных уравнений."
      }
    >
      <div className="lab__result">
        <table className="lab__table lab-5__table">
          <thead>
          <tr>
            {columns.map((column, columnIndex) => (
              <th
                key={columnIndex}
                className={"lab__table-th lab-5__table-th lab__table-th_width_" + columns.length + (column.type ? " lab__table_th_type_" + column.type : "")}
              >
                <InlineMath>{column.value}</InlineMath>
              </th>
            ))}
          </tr>
          </thead>
          <tbody>
          {
            resultX.map((x, index) => (
              <tr key={index}>
                <td className="lab__table-td">{index}</td>
                <td className="lab__table-td">{x.toFixed(rounding)}</td>
                <td className={"lab__table-td"  + (index === resultXmaxIndex ? " lab__table-td_type_result" : "")}>{resultY[index].toFixed(rounding)}</td>
                <td className="lab__table-td">{resultDeltaY[index] && resultDeltaY[index].toFixed(rounding)}</td>
                <td className="lab__table-td">{resultF[index] && resultF[index].toFixed(rounding)}</td>
                <td className="lab__table-td">{resultQ[index] && resultQ[index].toFixed(rounding)}</td>
                <td className="lab__table-td">{resultDeltaQ[index] && resultDeltaQ[index].toFixed(rounding)}</td>
                <td className="lab__table-td">{resultDelta2Q[index] && resultDelta2Q[index].toFixed(rounding)}</td>
                <td className="lab__table-td">{resultDelta3Q[index] && resultDelta3Q[index].toFixed(rounding)}</td>
              </tr>
            ))
          }
          </tbody>
        </table>
      </div>

    </Lab>
  )
};
