/*
* @params
* f - функция
* a - нижний предел интегрирования
* b - верхний предел интегрирования
* n - количество отрезков
* type - тип вычисления: "accurate"/"simple" (точный/простой)
* */
export default function integrateWithSimpsonMethod(f, a, b, n = 10, type = "accurate") {
  const h = (b - a) / n;

  const x = [a];
  for (let i = 1; i <= n; ++i) {
    x[i] = x[i - 1] + h;
  }

  if (type === "accurate") {

    let sum1 = 0, sum2 = 0;
    for (let i = 1; i < n; ++i) {
      sum1 += f(x[i]);
    }
    for (let i = 1; i <= n; ++i) {
      sum2 += f((x[i - 1] + x[i]) / 2);
    }

    return (
      h / 3 * (1 / 2 * f(x[0]) + sum1 + 2 * sum2 + 1 / 2 * f(x[n]))
    );
  } else if (type === "simple") {

    let sum = 0;
    for (let i = 0; i < n - 1; ++i) {
      sum += f(x[i]) + 4 * f(x[i] + h / 2) + f(x[i + 1]);
    }

    return (h / 6) * sum;
  }
}
