import { derivative, evaluate, parse } from "mathjs";
import React, { useState } from "react";
import { InlineMath } from "react-katex";

import createIntegral from "../../../helpers/createIntegral";
import getInaccuracyScore from "../../../helpers/getInaccuracyScore";
import integrateWithSimpsonMethod from "../../../helpers/integrateWithSimpsonMethod";

import plot from "../../../assets/lab2/task_b_plot.png";
import plot_derivative_2 from "../../../assets/lab2/task_b_plot_derivative_2.png";

const Lab2ResultB = ({ params, rounding: roundingProp }) => {
  const
    [func] = useState(params[0].value),
    [a] = useState(params[1].value),
    [b] = useState(params[2].value),
    [n] = useState(params[3].value),

    [funcDerivative1] = useState(derivative(parse(func), "x", { simplify: false })),
    [funcDerivative1_simplified] = useState(derivative(parse(func), "x")),
    [funcDerivative2] = useState(derivative(funcDerivative1, "x", { simplify: false })),
    [funcDerivative2_simplified] = useState(derivative(funcDerivative1_simplified, "x")),
    [funcDerivative3_simplified] = useState(derivative(funcDerivative2_simplified, "x")),
    [funcDerivative4_simplified] = useState(derivative(funcDerivative3_simplified, "x")),
    [funcDerivative4_max] = useState(Math.max(
      funcDerivative4_simplified.evaluate({ x: a }),
      funcDerivative4_simplified.evaluate({ x: b })
    )),

    [integral] = useState(createIntegral(func, "x")),
    [integralDefinite] = useState(createIntegral(func, "x", true, a, b)),
    [integralResultExpression] = useState("\\dfrac{\\ln\\left(2\\right)\\ln\\left(\\left|x\\right|\\right)-\\operatorname{Li}_2\\left(-\\frac{x}{2}\\right)}{\\ln\\left(10\\right)}"),
    [integralResultValue] = useState("0.2816126979296375"),

    [interval] = useState("[" + a + ";" + b + "]"),
    [rounding] = useState(8 || roundingProp),

    [simpson_step] = useState(((b - a) / getInaccuracyScore(a, b, n, funcDerivative4_max))),
    [simpson_accurate] = useState(integrateWithSimpsonMethod(x => evaluate(func, { x }), a, b, Math.ceil(simpson_step))),
    [simpson_simple] = useState(integrateWithSimpsonMethod(x => evaluate(func, { x }), a, b, simpson_step, "simple"));

  return (
    <div className="lab__result">
      <p className="lab__result-title">b) Функция <InlineMath>{parse(func).toTex()}</InlineMath></p>
      <div className="lab__result-blocks">
        <div className="lab__result-block">
          <p className="lab__result-block-title">Исследование функции</p>
          <p>График функции</p>
          <img src={plot} alt="" className="lab__result-plot"/>
          <p>
            <InlineMath>{"f'(x) = " + funcDerivative1.toTex() + "=" + funcDerivative1_simplified.toTex()}</InlineMath>
          </p>
          <p>
            <InlineMath>{"f''(x) = " + funcDerivative2.toTex() + "=" + funcDerivative2_simplified.toTex()}</InlineMath>
          </p>
          <p>График производной функции порядка 2</p>
          <img src={plot_derivative_2} alt="" className="lab__result-plot"/>
          <p>
            <InlineMath>{"f'''(x) =" + funcDerivative3_simplified.toTex()}</InlineMath>
          </p>
          <p>
            <InlineMath>{"f^{IV}(x) = " + funcDerivative4_simplified.toTex()}</InlineMath>
          </p>
          <p>
            <InlineMath>{"f^{IV}(" + a + ") = " + funcDerivative4_simplified.evaluate({ x: a }).toFixed(rounding)}</InlineMath>
          </p>
          <p>
            <InlineMath>{"f^{IV}(" + ((a + b) / 2) + ") = " + funcDerivative4_simplified.evaluate({ x: ((a + b) / 2) }).toFixed(rounding)}</InlineMath>
          </p>
          <p>
            <InlineMath>{"f^{IV}(" + b + ") = " + funcDerivative4_simplified.evaluate({ x: b }).toFixed(rounding)}</InlineMath>
          </p>
          <p>
            <InlineMath>{"M = max_{x\\in" + interval + "}|f''(x)| = " + funcDerivative4_max.toFixed(rounding) + " (x = " + a + ")"}</InlineMath>
          </p>
          <p><InlineMath>{integral + "=" + integralResultExpression}</InlineMath></p>
          <p><InlineMath>{integralDefinite + "=" + integralResultValue}</InlineMath></p>
        </div>

        <div className="lab__result-block">
          <p className="lab__result-block-title">Метод Симпсона</p>
          <p>
            Шаг:
            <InlineMath>{
              "\\frac"
              + `{${b} - ${a}}`
              + `{\\sqrt[4]{\\frac` + (
                `{180 * ${n}}`
                + `{${funcDerivative4_max} * \\left|${b}-${a}\\right|}`
                + `}}`
              ) + `=${simpson_step.toFixed(rounding)}\\approx${Math.ceil(simpson_step)}`
            }</InlineMath>
          </p>
          <p>
            Простая формула:
            <InlineMath>{integralDefinite + "=" + simpson_simple.toFixed(rounding)}</InlineMath>
          </p>
          <p>Точная формула:
            <InlineMath>{integralDefinite + "=" + simpson_accurate.toFixed(rounding)}</InlineMath>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Lab2ResultB;
