import { derivative, evaluate, parse } from "mathjs";
import React, { useState } from "react";
import { InlineMath } from "react-katex";

import createIntegral from "../../../helpers/createIntegral";
import getInaccuracyRunge from "../../../helpers/getInaccuracyRunge";
import integrateWithRectangleMethod from "../../../helpers/integrateWithRectangleMethod";
import integrateWithTrapezeMethod from "../../../helpers/integrateWithTrapezeMethod";

import lab_a_plot from "../../../assets/lab2/task_a_plot.png";

const Lab2ResultA = ({ params, rounding }) => {
  const
    [func] = useState(params[0].value),
    [a] = useState(params[1].value),
    [b] = useState(params[2].value),
    [n] = useState(params[3].value),

    [funcDerivative1] = useState(derivative(parse(func), "x", { simplify: false })),
    [funcDerivative1_simplified] = useState(derivative(parse(func), "x")),
    [funcDerivative2] = useState(derivative(funcDerivative1, "x", { simplify: false })),
    [funcDerivative2_simplified] = useState(derivative(funcDerivative1, "x")),
    [funcDerivative2_max] = useState(2),

    [integral] = useState(createIntegral(func, "x")),
    [integralDefinite] = useState(createIntegral(func, "x", true, a, b)),
    [integralResultExpression] = useState("\\dfrac{\\sqrt{{2\\pi}}\\operatorname{S}\\left(\\frac{\\sqrt{2}x}{\\sqrt{{\\pi}}}\\right)}{2}"),

    [integralResultValue] = useState("0.3102683017233"),
    [interval] = useState("[" + a + ";" + b + "]"),

    [rectangle_h] = useState(integrateWithRectangleMethod(x => evaluate(func, { x }), a, b, n)),
    [rectangle_2h] = useState(integrateWithRectangleMethod(x => evaluate(func, { x }), a, b, n / 2)),
    [trapeze_h] = useState(integrateWithTrapezeMethod(x => evaluate(func, { x }), a, b, n)),
    [trapeze_2h] = useState(integrateWithTrapezeMethod(x => evaluate(func, { x }), a, b, n / 2));

  return (
    <div className="lab__result">
      <p className="lab__result-title">a) Функция <InlineMath>{parse(func).toTex()}</InlineMath></p>
      <div className="lab__result-blocks">

        <div className="lab__result-block">
          <p className="lab__result-block-title">Исследование функции</p>
          <p>
            <InlineMath>{"f'(x) = " + funcDerivative1.toTex() + "=" + funcDerivative1_simplified.toTex()}</InlineMath>
          </p>
          <p>
            <InlineMath>{"f''(x) = " + funcDerivative2.toTex() + "=" + funcDerivative2_simplified.toTex()}</InlineMath>
          </p>
          <p>График производной функции порядка 2</p>
          <img src={lab_a_plot} alt="" className="lab__result-plot"/>
          <p>
            <InlineMath>{"M = max_{x\\in" + interval + "}|f''(x)| = " + funcDerivative2_max + " (x = " + a + ")"}</InlineMath>
          </p>
          <p><InlineMath>{integral + "=" + integralResultExpression}</InlineMath> интеграл Френеля</p>
          <p><InlineMath>{integralDefinite + "=" + integralResultValue}</InlineMath></p>
        </div>

        <div className="lab__result-block">
          <p className="lab__result-block-title">Метод прямоугольников</p>
          <p>
            <InlineMath>{"I(h)=" + integralDefinite + "=" + rectangle_h.toFixed(rounding)}</InlineMath>
          </p>
          <p>
            <InlineMath>{"I(2h)=" + integralDefinite + "=" + rectangle_2h.toFixed(rounding)}</InlineMath>
          </p>
          <p>Погрешность по правилу Рунге</p>
          <p>
            <InlineMath>{"I(h) - I(2h) \\approx" + getInaccuracyRunge(rectangle_h, rectangle_2h, 2).toFixed(rounding)}</InlineMath>
          </p>
        </div>

        <div className="lab__result-block">
          <p className="lab__result-block-title">Метод трапеций</p>
          <p>
            <InlineMath>{"I(h)=" + integralDefinite + "=" + trapeze_h.toFixed(rounding)}</InlineMath>
          </p>
          <p>
            <InlineMath>{"I(2h)=" + integralDefinite + "=" + trapeze_2h.toFixed(rounding)}</InlineMath>
          </p>
          <p>Погрешность по правилу Рунге</p>
          <p>
            <InlineMath>{"I(h) - I(2h) \\approx" + getInaccuracyRunge(trapeze_h, trapeze_2h, 4).toFixed(rounding)}</InlineMath>
          </p>
        </div>
      </div>
    </div>
  )
};

export default Lab2ResultA;
