import React from "react";
import { parse } from "mathjs";
import { InlineMath, BlockMath } from "react-katex";

import "./lab-tasks.scss";

const LabTasks = ({ tasks = [] }) => (
  <div className="lab-tasks">
    <p className="lab-tasks__title">Задани{tasks.length > 1 ? "я" : "е"}:</p>
    {
      tasks.map((task, taskIndex) => (
        <div key={"task-" + taskIndex} className="lab-tasks__task">
          <p className="lab-tasks__task-condition">{task.condition}</p>
          {task.formula && <BlockMath>{task.formula}</BlockMath>}
          {task.params && task.params.length && (
            <table className="lab__table">
              <thead>
              <tr>
                {task.params.map((param, paramIndex) => (
                  <th key={"param-" + paramIndex}
                      className={"lab__table-th"
                      + (" lab__table-th_width_" + task.params.length)
                      + (param.type ? " lab__table-th_type_" + param.type : "")
                      }
                  >
                    {param.name}
                  </th>
                ))}
              </tr>
              </thead>
              <tbody>
              <tr>
                {task.params.map((param, paramIndex) => (
                  <td
                    key={"param-" + paramIndex}
                    className={"lab__table-td"
                    + (" lab__table-td_width_" + task.params.length)
                    + (param.type ? " lab__table-td_type_" + param.type : "")}
                  >
                    {param.type === "formula"
                      ? (
                        <InlineMath>
                          {parse(param.value).toTex().replace(param.replaceValue, param.replaceFunction)}
                        </InlineMath>
                      )
                      : param.value}
                  </td>
                ))}
              </tr>
              </tbody>
            </table>
          )}
        </div>
      ))
    }
  </div>
);

export default LabTasks;
