import { evaluate, parse } from "mathjs";
import React, { useState } from "react";
import { InlineMath } from "react-katex";

import integrateWithGaussMethod from "../../../helpers/integrateWithGaussMethod";
import createIntegral from "../../../helpers/createIntegral";

const Lab2ResultC = ({ params, rounding: roundingProp }) => {
  const
    [func] = useState(params[0].value),
    [a] = useState(params[1].value),
    [b] = useState(params[2].value),
    [n] = useState(params[3].value),

    [integral] = useState(createIntegral(func, "x")),
    [integralDefinite] = useState(createIntegral(func, "x", true, a, b)),
    [integralResultExpression] = useState("\\dfrac{x\\left(\\left(10x-16\\right)\\ln\\left(x\\right)-5x+16\\right)}{20}"),
    [integralResultValue] = useState("2.07846068010242"),

    [rounding] = useState(9 || roundingProp),

    [gaussResult] = useState(integrateWithGaussMethod(x => evaluate(func, { x }), a, b, n)),
    [gaussIntegralValue] = useState(gaussResult[0]),
    [gaussCoefsT] = useState(gaussResult[1].t),
    [gaussCoefsA] = useState(gaussResult[1].a),
    [gaussX] = useState(gaussResult[2]),
    [gaussY] = useState(gaussResult[3]);

  return (
    <div className="lab__result">
      <p className="lab__result-title">c) Функция <InlineMath>{parse(func).toTex()}</InlineMath></p>
      <div className="lab__result-blocks">

        <div className="lab__result-block">
          <p className="lab__result-block-title">Исследование функции</p>
          <p><InlineMath>{integral + "=" + integralResultExpression}</InlineMath></p>
          <p><InlineMath>{integralDefinite + "=" + integralResultValue}</InlineMath></p>
        </div>

        <div className="lab__result-block">
          <p className="lab__result-block-title">Метод Гаусса</p>
          <p>Табличные весовые коэффициенты метода Гаусса и полученные точки</p>
          <table>
            <thead>
            <tr>
              <th>№</th>
              <th>t</th>
              <th>a</th>
              <th>x</th>
              <th>y</th>
            </tr>
            </thead>
            <tbody>
            {gaussCoefsT.map((coef, coefIndex) => (
              <tr key={coefIndex}>
                <td>{coefIndex + 1}</td>
                <td>{coef.toFixed(rounding)}</td>
                <td>{gaussCoefsA[coefIndex].toFixed(rounding)}</td>
                <td>{gaussX[coefIndex].toFixed(rounding)}</td>
                <td>{gaussY[coefIndex].toFixed(rounding)}</td>
              </tr>
            ))}
            </tbody>
          </table>
          <p>
            <InlineMath>{integralDefinite + "=" + gaussIntegralValue.toFixed(rounding)}</InlineMath>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Lab2ResultC;
