import React, { useState } from "react";

import Lab from "../../components/lab";
import Lab3ResultA from "./lab3ResultA";
import Lab3ResultB from "./lab3ResultB";

import "./lab3.scss";

const Lab3 = () => {
  const [tests] = useState([
    {
      params: [
        {
          name: "y - 2*x/y",
          value: "y - 2*x/y",
          type: "formula"
        },
        { name: "a", value: 0 },
        { name: "b", value: 1 },
        { name: "h", value: 0.2 },
        { name: "x0", value: 0 },
        { name: "y0", value: 1 },
      ]
    },
    {
      params: []
    }
  ]);

  const [tasks] = useState([
    {
      condition: "Решить задачу Коши методом Эйлера. Начальные условия: y(0) = 1; отрезок [0, 1]; шаг h = 0.1",
      formula: "2x^2 + 2y",
      params: [
        {
          name: "2x^2 + 2y",
          value: "2*x^2 + 2*y",
          type: "formula"
        },
        { name: "a", value: 0 },
        { name: "b", value: 1 },
        { name: "h", value: 0.1 },
        { name: "x0", value: 0 },
        { name: "y0", value: 1 },
      ]
    },
    // {
    //   condition: "b) Решить задачу Коши для системы ОДУ методом Эйлера. Начальные условия: отрезок [0, 2]; шаг h = 0.2",
    //   formula: `\\begin{cases} u'' = -v + 3u, \\\\ v''=v - 2u \\end{cases}`,
    //   params: [
    //     { name: "u(0)", value: -1 },
    //     { name: "u'(0)", value: 1 },
    //     { name: "v(0)", value: -1.5 },
    //     { name: "v'(0)", value: 3 },
    //     { name: "h", value: 0.2 }
    //   ]
    // }
  ]);

  const [rounding] = useState(6);

  // y' - y*tg(x) = -2/3 * y^4 * sin(x), y(0) = 1, a = 0, b = 1.2
  // Выразим y': y' = y*tg(x) - 2/3 * y^4 * sin(x)
  // const f = (x, y) => y * Math.tan(x) - 2 / 3 * Math.pow(y, 4) * Math.sin(x);

  return (
    <Lab
      title="Лабораторная работа 3. Метод Эйлера"
      goal={""}
      tasks={tasks}
      conclusion={
        "В ходе работы был изучен метод Эйлера для решения дифференциальных уравнений."
        + " На основе полученных теоретических знаний были получены практические навыки благодаря применению метода для решения диффенциальных уравнений."
      }
    >
      <Lab3ResultA params={tasks[0].params} rounding={rounding} testParams={tests[0].params}/>
      {false && <Lab3ResultB params={tasks[1].params} rounding={rounding} testParams={tests[1].params}/>}
    </Lab>
  )
};

export default Lab3;
