import React, { useState } from "react";
import { InlineMath } from "react-katex";

import Lab from "../../components/lab";

import TableInput from "../../components/table-input";
import TableResult from "../../components/table-result";

import plot_1 from "./../../../assets/lab1/plot_1.png";
import plot_2 from "./../../../assets/lab1/plot_2.png";
import plot_3 from "./../../../assets/lab1/plot_3.png";
import plot_4 from "./../../../assets/lab1/plot_4.png";

export default function Lab1() {
  const [tasks] = useState([
    {
      condition: "Вычислить точное и приближенное (тремя методами) значения производной функции. Значения параметров A, a, ω, b выбрать самостоятельно.",
      formula: "y(t)=Ae^{at} \\cos(ωt+b)",
      syntax: "tex"
    }
  ]);
  const [func] = useState(tasks[0].formula);
  const [funcDerivative] = useState("y'(t)=A(e^{at}a\\cos(ωt+b) - e^{at}ω\\sin(ωt+b))=Ae^{at}(a\\cos(ωt+b) - ω\\sin(ωt+b))");

  const [params, setParams] = useState({
    "x": 0, // начальная точка

    "A": 1, // коэффициенты
    "a": 1, //  для
    "w": 1, //   функций
    "b": 1, //    y и dy

    "accuracy": 4, // кол-во точек после запятой при округлении
  });
  const { x, A, a, w, b, accuracy } = params;

  const
    y = (t) => (A * Math.exp(a * t) * Math.cos(w * t + b)),
    dy = (t) => (A * (Math.exp(a * t) * a * Math.cos(w * t + b) - Math.exp(a * t) * Math.sin(w * t + b) * w));

  const columns = [
    { value: "Δx", type: "formula", func: x => x },
    { value: "y(x)", type: "formula", func: x => y(x) },
    { value: "y'(x)", type: "formula", func: x => dy(x) },
    { value: "(y(x + Δx) - y(x)) / Δx", type: "formula", func: (x, dx) => ((y(x + dx) - y(x)) / dx) },
    { value: "(y(x) - y(x - Δx)) / Δx", type: "formula", func: (x, dx) => ((y(x) - y(x - dx)) / dx) },
    {
      value: "(y(x + Δx) - y(x - Δx)) / 2Δx",
      type: "formula",
      func: (x, dx) => (y(x + dx) - y(x - dx)) / (2 * dx)
    }
  ];
  const deltasX = [1, 0.2, 0.1, 0.01, 0.001];

  return (
    <Lab
      title="Лабораторная работа 1. Численное дифференцирование"
      goal={"изучение различных методов нахождения производной первого порядка и практическое дифференцирование."}
      tasks={tasks}
      conclusion={"В ходе работы были изучены 3 метода нахождения приближенного значения производной функции."
      + " На основе полученных теоретических знаний были получены практические навыки благодаря самостоятельным расчетам."}
    >
      <div className="lab__result">
        <div className="lab__result-blocks">
          <div className="lab__result-block">
            <p className="lab__result-block-title">Исследование функции</p>
            <p>
              <InlineMath>{func}</InlineMath>
            </p>
            <p>
              <InlineMath>{funcDerivative}</InlineMath>
            </p>
          </div>
          <div className="lab__result-block">
            <p className="lab__result-block-title">Ввод значений параметров для расчетов</p>
            <TableInput
              params={params}
              onChange={(id, value) => setParams(p => ({ ...p, [id]: parseInt(value) || 0 }))}
            />
          </div>
          <div className="lab__result-block">
            <p className="lab__result-block-title">Результаты расчетов по трем методам</p>
            <TableResult
              columns={columns}
              rows={deltasX}
              x={x}
              accuracy={accuracy}
            />
          </div>
          <div className="lab__result-block">
            <p className="lab__result-block-title">Графики функции</p>
            <div className="lab__result-plots lab__result-plots_direction_row lab__result-plots_justify_space-between">
            <img src={plot_1} alt="" className="lab__result-plot"/>
            <img src={plot_2} alt="" className="lab__result-plot"/>
            <img src={plot_3} alt="" className="lab__result-plot"/>
            <img src={plot_4} alt="" className="lab__result-plot"/>
            </div>
          </div>
        </div>
      </div>
    </Lab>
  )
};
