/*
* @params
* f - функция
* a - нижний предел интегрирования
* b - верхний предел интегрирования
* n - количество отрезков
* */
export default function IntegrateWithTrapezeMethod(f, a, b, n) {
  const h = (b - a) / n;

  const x = [a];
  for (let i = 1; i <= n; ++i) {
    x[i] = x[i - 1] + h;
  }

  let sum = 0;
  for (let i = 1; i < n; ++i) {
    sum += f(x[i - 1]);
  }

  return h * (f(x[0]) / 2 + sum + f(x[n]) / 2);
}
