import React from "react";
import { BrowserRouter } from "react-router-dom";

import { Router } from "./../../router";

import "./app.scss";

import Nav from "../nav/nav";

const App = () => {
  return (
    <BrowserRouter>
      <div>
        <Nav/>
        <Router/>
      </div>
    </BrowserRouter>
  )
};

export default App;
