const coefs = {
  "5": {
    "t": [.90617, .53846, 0],
    "a": [.23692, .47862, .56888]
  },
  "10": {
    "t": [.9739065285, .8650633666, .6794095682, .4333953941, .1488743389],
    "a": [.0666713443, .1494513491, .2190863625, .2692667193, .2955242247]
  }
};

const getCoefs = n => {
  const
    coefsData = coefs[n.toString()],
    positiveCoefsCount = Math.ceil(n / 2);

  const result = {};
  for (let coefId in coefsData) {
    const array = coefsData[coefId];

    result[coefId] = coefId === "t"
      ? Array.from({ length: n }).map((_, index) => index < positiveCoefsCount
        ? array[index]
        : -array[n - 1 - index]
      )
      : Array.from({ length: n }).map((_, index) => index < positiveCoefsCount
        ? array[index]
        : array[n - 1 - index]
      )
  }
  return result;
};

export default function integrateWithGaussMethod(f, a, b, n) {
  const coefs = getCoefs(n);

  const
    base1 = (b + a) / 2,
    base2 = (b - a) / 2;

  const x = [];
  for (let i = 0; i < n; ++i) {
    x[i] = base1 + coefs.t[i] * base2;
  }

  let sum = 0;
  const y = [];
  for (let i = 0; i < n; ++i) {
    y[i] = coefs.a[i] * f(x[i]);
    sum += y[i];
  }

  return [base2 * sum, coefs, x, y];
}
