/*
* @params
* f - функция
* a - начало отрезка
* b - конец отрезка
* h - шаг
* x0 - начальное условие Коши для x
* y0 - начальное условие Коши для y
* */
export default function solveCauchyProblemWithEulerMethod(f, a, b, h, x0, y0) {
  const n = (b - a) / h;

  const x = [x0];
  for (let i = 1; i <= n; ++i) {
    x[i] = x[i - 1] + h;
  }

  const y = [y0];
  for (let i = 1; i <= n; ++i) {
    y[i] = y[i - 1] + h * f(x[i - 1], y[i - 1]);
  }

  return [x, y];
}
