import React from "react";

import LabTasks from "../lab-tasks";

import "./lab.scss";

const Lab = ({ children, conclusion, goal, id, tasks, title }) => (
  <div className={"lab" + (id ? " lab-" + id : "")}>
    <h1 className="lab__title">{title}</h1>

    <div className="lab__goal">
      <span className="lab__goal-title">Цель работы: </span><span className="lab__goal-text">{goal}</span>
    </div>

    <LabTasks tasks={tasks}/>

    <div className="lab__work">
      <p className="lab__work-title">Выполнение работы</p>
      {children}
    </div>

    <div className="lab__conclusion">
      <p className="lab__conclusion-title">Вывод</p>
      {conclusion}
    </div>
  </div>
);

export default Lab;
