import React, { useState } from "react";

import Lab from "../../components/lab";
import Lab2ResultA from "./lab2ResultA";
import Lab2ResultB from "./lab2ResultB";
import Lab2ResultC from "./lab2ResultC";

import "./lab2.scss";

export default function Lab2() {
  const [rounding] = useState(6);
  const [tasks] = useState([
    {
      condition: "a) Вычислить по формуле прямоугольников и трапеций интеграл от функции f(x) в границах от а до b при заданном n и оценить погрешность. Сравнить полученные результаты.",
      params: [
        { name: "Функция", value: "sin(x^2)", type: "formula" },
        { name: "a", value: -1 },
        { name: "b", value: 0 },
        { name: "n", value: 20 }
      ]
    },
    {
      condition: "b) Вычислить по формуле Симпсона интеграл от функции f(x) в границах от а до b с точностью, равной 0.0001.",
      params: [
        { name: "Функция", value: "log(x + 2, 10) / x", type: "formula" },
        { name: "a", value: 1.2 },
        { name: "b", value: 2.0 },
        { name: "eps", value: 0.0001 }
      ]
    },
    {
      condition: "c) Вычислить интеграл функции f(x) по формуле Гаусса:",
      params: [
        { name: "Функция", value: "(x - 0.8) * log(x, e)", type: "formula" },
        { name: "a", value: 2.3 },
        { name: "b", value: 3.3 },
        { name: "n", value: 10 }
      ]
    },
  ]);

  return (
    <Lab
      title="Лабораторная работа 2. Численное интегрирование"
      goal="изучение различных методов вычисления определенных интегралов, практическое интегрирование функций на ЭВМ."
      id={2}
      tasks={tasks}
      conclusion={"В ходе работы были изучены следующие методы вычисления определенных интегралов: прямоугольников, трапеций, Симпсона, Гаусса."
      + " Помимо этого, изучен способ определения оценки погрешности по правилу Рунге."
      + " На основе полученных теоретических знаний были получены практические навыки благодаря самостоятельным расчетам."}
    >
      <Lab2ResultA params={tasks[0].params} rounding={rounding}/>
      <Lab2ResultB params={tasks[1].params} rounding={rounding}/>
      <Lab2ResultC params={tasks[2].params} rounding={rounding}/>
    </Lab>
  )
};
