export default function solveRungeKutta(a, b, h, func, x0, y0) {
  const n = (b - a) / h;

  const x = [x0];
  for (let i = 1; i <= n; ++i) {
    x[i] = x[i - 1] + h;
  }

  const y = [y0];
  for (let i = 0; i < n; ++i) {
    const
      k1 = h * func(x[i], y[i]),
      k2 = h * func(x[i] + h/2, y[i] + k1/2),
      k3 = h * func(x[i] + h/2, y[i] + k2/2),
      k4 = h * func(x[i] + h, y[i] + k3);

    y[i + 1] = y[i] + 1 / 6 * (k1 + 2 * k2 + 2 * k3 + k4);
  }

  return [x, y];
}
