import solveRungeKutta from "./solveRungeKutta";

export default function solveMiln(a, b, h, func, x0, y0) {
  const
    resultRungeKutta = solveRungeKutta(a, b, h, func, x0, y0),
    resultRungeKuttaX = resultRungeKutta[0],
    resultRungeKuttaY = resultRungeKutta[1];

  const
    precisionFunc = (y_4, h, f_3, f_2, f_1) => y_4 + 4 * h / 3 * (2 * f_3 - f_2 + 2 * f_1),
    correctionFunc = (y_2, f_2, f_1, f_prec) => y_2 + h / 3 * (f_2 + 4 * f_1 + f_prec);

  const
    n = (b - a) / h,
    baseIterationsCount = 3;

  const
    x = [],
    y = [],
    f = [];

  for (let i = 0; i <= baseIterationsCount; ++i) {
    x[i] = resultRungeKuttaX[i];
    y[i] = resultRungeKuttaY[i];
    f[i] = func(x[i], y[i]);
  }

  for (let i = baseIterationsCount + 1; i <= n; ++i) {
    x[i] = x[i - 1] + h;
    y[i] = precisionFunc(y[i - 4], h, f[i - 3], f[i - 2], f[i - 1]);
    y[i] = correctionFunc(y[i - 2], f[i - 2], f[i - 1], func(x[i], y[i]));
    f[i] = func(x[i], y[i]);
  }

  return [x, y, f];
}
