import solveRungeKutta from "./solveRungeKutta";

export default function solveAdams(a, b, h, func, x0, y0) {
  const
    resultRungeKutta = solveRungeKutta(a, b, h, func, x0, y0),
    resultRungeKuttaX = resultRungeKutta[0],
    resultRungeKuttaY = resultRungeKutta[1];

  const extrapolationAdamsFuncDelta = (q, deltaQ, deltaSquareQ, deltaCubeQ) => q + 1 / 2 * deltaQ + 5 / 12 * deltaSquareQ + 3 / 8 * deltaCubeQ;
  // const extrapolationAdamsFunc = (y, i) => y[i - 1] + h / 24 * (55 * f[i - 1] - 59 * f[i - 2] + 37 * f[i - 3] - 9 * f[i - 4]);
  // const interpolationAdamsFunc = (y, i) => y[i - 1] + h / 24 * (9 * f[i] + 19 * f[i - 1] - 5 * f[i - 2] + f[i - 3]);

  const
    n = (b - a) / h,
    baseIterationsCount = 3;

  const
    x = [],
    y = [],
    deltaY = [],
    f = [],
    q = [],
    deltaQ = [],
    deltaSquareQ = [],
    deltaCubeQ = [];

  // До ломаной линии
  for (let i = 0; i <= baseIterationsCount; ++i) {
    x[i] = resultRungeKuttaX[i];
    y[i] = resultRungeKuttaY[i];
    f[i] = func(x[i], y[i]);
    q[i] = h * f[i];
  }

  for (let i = 0; i < baseIterationsCount; ++i) {
    // deltaY[i] = y[i + 1] - y[i];
    deltaQ[i] = q[i + 1] - q[i];
  }

  for (let i = 0; i < baseIterationsCount - 1; ++i) {
    deltaSquareQ[i] = deltaQ[i] * deltaQ[i];
  }

  for (let i = 0; i < baseIterationsCount - 2; ++i) {
    deltaCubeQ[i] = deltaSquareQ[i] * deltaQ[i];
  }

  // После ломаной линии
  deltaY[baseIterationsCount] = extrapolationAdamsFuncDelta(q[baseIterationsCount], deltaQ[baseIterationsCount - 1], deltaSquareQ[baseIterationsCount - 2], deltaCubeQ[baseIterationsCount - 3]);

  for (let i = baseIterationsCount + 1; i <= n; ++i) {
    x[i] = x[i - 1] + h;

    y[i] = y[i - 1] + deltaY[i - 1];
    // y[i] = extrapolationAdamsFunc(y, i);
    f[i] = func(x[i], y[i]);
    // y[i] = interpolationAdamsFunc(y, i);

    q[i] = h * f[i];
    deltaQ[i - 1] = q[i] - q[i - 1];
    deltaSquareQ[i - 2] = deltaQ[i - 1] * deltaQ[i - 1];
    deltaCubeQ[i - 3] = deltaSquareQ[i - 2] * deltaQ[i - 1];

    deltaY[i] = extrapolationAdamsFuncDelta(q[i], deltaQ[i - 1], deltaSquareQ[i - 2], deltaCubeQ[i - 3]);
  }

  return [x, y, deltaY, f, q, deltaQ, deltaSquareQ, deltaCubeQ];
}
