import { evaluate, parse } from "mathjs";
import React, { useCallback, useState } from "react";

import solveCauchyProblemWithEulerMethod from "../../../helpers/solveCauchyProblemWithEulerMethod";
import { InlineMath } from "react-katex";

const Lab3ResultA = ({ params, rounding, testParams }) => {
  // Test
  const [eulerResultTest] = useState(solveCauchyProblemWithEulerMethod(
    (x, y) => evaluate(testParams[0].value, {
      x,
      y
    }), testParams[1].value, testParams[2].value, testParams[3].value, testParams[4].value, testParams[5].value)
  );
  const [eulerResultTestX] = useState(eulerResultTest[0]);
  const [eulerResultTestY] = useState(eulerResultTest[1]);
  const [eulerDeltasTestY] = useState(() => {
    const deltas = [];
    for (let i = 0; i < eulerResultTestY.length - 1; ++i) {
      deltas[i] = (eulerResultTestY[i + 1] - eulerResultTestY[i]).toFixed(rounding);
    }
    deltas.push("");
    return deltas;
  });
  const cauchySolveTest = useCallback(x => Math.pow(2* x + 1, .5), []);
  const [truncationAbsoluteTest] = useState(Math.abs(cauchySolveTest(eulerResultTestX[eulerResultTestX.length - 1]) - eulerResultTestY[eulerResultTestX.length - 1]));
  const [truncationRelativeTest] = useState(Math.abs(truncationAbsoluteTest / cauchySolveTest(eulerResultTestX[eulerResultTestX.length - 1])));
  const [columnsTest] = useState([
    { name: "i", type: "formula", syntax: "tex" },
    { name: "x_i", type: "formula", syntax: "tex" },
    { name: "y_i", type: "formula", syntax: "tex" },
    { name: "Δy_i", type: "formula", syntax: "tex" },
    { name: "\\sqrt{2x+1}", type: "formula", syntax: "tex" },
  ]);

  const [func] = useState(params[0].value);
  const [eulerResult] = useState(solveCauchyProblemWithEulerMethod(
    (x, y) => evaluate(func, {
      x,
      y
    }), params[1].value, params[2].value, params[3].value, params[4].value, params[5].value)
  );
  const [eulerResultX] = useState(eulerResult[0]);
  const [resultXmaxIndex] = useState(eulerResultX.length - 1);
  const [eulerResultY] = useState(eulerResult[1]);
  const [eulerDeltasY] = useState(() => {
    const deltas = [];
    for (let i = 0; i < eulerResultY.length - 1; ++i) {
      deltas[i] = (eulerResultY[i + 1] - eulerResultY[i]).toFixed(rounding);
    }
    deltas.push("");
    return deltas;
  });
  const [cauchySolveTex] = useState("y=(-\\dfrac{\\left(2x^2+2x+1\\right)\\mathrm{e}^{-2x}}{2}+\\frac{3}{2})e^{2x}");
  const cauchySolve = useCallback(x => (-(2 * Math.pow(x, 2) + 2 * x + 1) * Math.exp(-2 * x) / 2 + 3 / 2) * Math.exp(2 * x), []);
  const [truncationAbsolute] = useState(cauchySolve(eulerResultX[eulerResultX.length - 1]) - eulerResultY[eulerResultX.length - 1]);
  const [truncationRelative] = useState(truncationAbsolute / cauchySolve(eulerResultX[eulerResultX.length - 1]));
  const [columns] = useState([
    { name: "i", type: "formula", syntax: "tex" },
    { name: "x_i", type: "formula", syntax: "tex" },
    { name: "y_i", type: "formula", syntax: "tex" },
    { name: "Δy_i", type: "formula", syntax: "tex" },
    { name: cauchySolveTex, type: "formula", syntax: "tex" },
  ]);

  const Table = ({ columns, eulerResultX, eulerResultY, eulerDeltasY, cauchySolve }) => (
    <table className="lab__table lab-3__table">
      <thead>
      <tr>
        {columns.map((column, columnIndex) => (
          <th
            key={columnIndex}
            className={"lab__table-th lab-3__table-th lab__table-th_width_" + columns.length + (column.type ? " lab__table_th_type_" + column.type : "")}
          >
            {column.type === "formula"
              ? column.syntax === "tex"
                ? <InlineMath>{column.name}</InlineMath>
                : <InlineMath>{parse(column.name).toTex()}</InlineMath>
              : column.value}
          </th>
        ))}
      </tr>
      </thead>
      <tbody>
      {
        eulerResultX.map((x, index) => (
          <tr key={index}>
            <td className="lab__table-td">{index}</td>
            <td className="lab__table-td">{x.toFixed(rounding)}</td>
            <td className="lab__table-td">{eulerResultY[index].toFixed(rounding)}</td>
            <td className="lab__table-td">{eulerDeltasY[index]}</td>
            <td className={"lab__table-td"  + (index === resultXmaxIndex ? " lab__table-td_type_result" : "")}>{cauchySolve(x).toFixed(rounding)}</td>
          </tr>
        ))
      }
      </tbody>
    </table>
  );

  return (
    <div className="lab__result">
      <p className="lab__result-title">Функция <InlineMath>{parse(func).toTex()}</InlineMath></p>
      <div className="lab__result-blocks">

        <div className="lab__result-block">
          <p className="lab__result-block-title">Исследование функции</p>
          <p>
            <InlineMath>{"y'=" + parse(func).toTex()}</InlineMath>
          </p>
          <p>
            <InlineMath>{"y=(-\\dfrac{\\left(2x^2+2x+1\\right)\\mathrm{e}^{-2x}}{2}+C_1)e^{2x}"}</InlineMath>
          </p>
          <p>Решение задачи Коши</p>
          <p>
            <InlineMath>{cauchySolveTex}</InlineMath>
          </p>
        </div>
        <div className="lab__result-block">
          <p className="lab__result-block-title">Тестовый пример</p>
          <Table columns={columnsTest} eulerResultX={eulerResultTestX} eulerResultY={eulerResultTestY} eulerDeltasY={eulerDeltasTestY} cauchySolve={cauchySolveTest}/>
          <p>Абсолютная погрешность</p>
          <p><InlineMath>{"\\varepsilon=" + truncationAbsoluteTest.toFixed(rounding)}</InlineMath></p>
          <p>Относительная погрешность</p>
          <p><InlineMath>{"\\varepsilon=" + (truncationRelativeTest * 100).toFixed(3) + "\\%"}</InlineMath></p>
        </div>
        <div className="lab__result-block">
          <p className="lab__result-block-title">Расчет функции</p>
          <Table columns={columns} eulerResultX={eulerResultX} eulerResultY={eulerResultY} eulerDeltasY={eulerDeltasY} cauchySolve={cauchySolve}/>
          <p>Абсолютная погрешность</p>
          <p><InlineMath>{"\\varepsilon=" + truncationAbsolute.toFixed(rounding)}</InlineMath></p>
          <p>Относительная погрешность</p>
          <p><InlineMath>{"\\varepsilon=" + (truncationRelative * 100).toFixed(3) + "\\%"}</InlineMath></p>
        </div>
      </div>
    </div>
  )
};

export default Lab3ResultA;
